import React from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { Container, Title, SubTitle, TextValidator } from 'styles'
import { EventPaymentWrapper, CounterWrapper, CounterButton, CounterText, PlusIcon, MinusIcon, Form, InputGroup } from './EventPayment.style'
import { Row, Column, colors, Text, Wrapper, Button } from 'styles'
import { buyEvent } from 'redux/actions/events.actions'
import ReactInputMask from 'react-input-mask'
import { withFormik } from 'formik'
import * as Yup from 'yup';

class PaymentForm extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            counter: 1,
            event: {}
        };
    }

    componentDidMount() {
        const { events, match } = this.props;
        const event = events.length ? events.find(event => event.id === parseInt(match.params.id)) : {};

        this.setState({ event });

    }

    increment = () => {
        this.setState({ counter: this.state.counter + 1 });
    }

    decrement = () => {
        if (this.state.counter > 1)
            this.setState({ counter: this.state.counter - 1 });
    }

    render() {
        const { event, counter } = this.state;
        const { touched, errors, values, handleChange, handleBlur } = this.props;

        return (
            <>
                <EventPaymentWrapper>
                    <Container>
                        <Title>Pagamento</Title>
                        <Row>
                            <Column flex={0.7}>
                                <Text>Inteira</Text>
                                <Text color={colors.orange} size={22}>{event.priceTicket ? event.priceTicket * counter : ''}</Text>
                            </Column>
                            <Column flex={0.2}>
                                <CounterWrapper>
                                    <CounterButton onClick={this.decrement}>
                                        <MinusIcon size={25} color={colors.lightGray} />
                                    </CounterButton>
                                    <CounterText>{counter}</CounterText>
                                    <CounterButton onClick={this.increment}>
                                        <PlusIcon size={25} color={colors.orange} />
                                    </CounterButton>
                                </CounterWrapper>
                            </Column>
                        </Row>
                    </Container>
                </EventPaymentWrapper>
                <Form>
                    <InputGroup>
                        <SubTitle>Dados do Pagamento</SubTitle>
                        <input
                            placeholder="E-mail" type="email"
                            value={values.Email}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            name="Email" />
                        {errors.Email && touched.Email && <TextValidator>{errors.Email}</TextValidator>}
                    </InputGroup>
                    <InputGroup>
                        <input
                            placeholder="Nome do Titular"
                            value={values.Name}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            name="Name" />
                        {errors.Name && touched.Name && <TextValidator>{errors.Name}</TextValidator>}
                    </InputGroup>
                    <InputGroup>
                        <ReactInputMask
                            mask="9999 9999 9999 9999"
                            placeholder="Número do Cartão"
                            maskChar=''
                            value={values.CardNumber}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            name="CardNumber" />
                        {errors.CardNumber && touched.CardNumber && <TextValidator>{errors.CardNumber}</TextValidator>}
                    </InputGroup>
                    <InputGroup>
                        <ReactInputMask
                            mask="99/99" placeholder="Data de Expiração" maskChar=''
                            value={values.ExpirationDate}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            name="ExpirationDate" />
                        {errors.ExpirationDate && touched.ExpirationDate && <TextValidator>{errors.ExpirationDate}</TextValidator>}
                    </InputGroup>
                    <InputGroup>
                        <ReactInputMask
                            mask="999" placeholder="Código" maskChar=''
                            value={values.Code}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            name="Code" />
                        {errors.Code && touched.Code && <TextValidator>{errors.Code}</TextValidator>}
                    </InputGroup>
                    <Wrapper justifyContent="center">
                        <Button color="orange" type="submit" onClick={this.props.handleSubmit}>Enviar Pagamento</Button>
                    </Wrapper>
                </Form>
            </>
        );
    }
}

const EventPayment = withFormik({
    mapPropsToValues: props => {
        return ({
            Email: '',
            Name: '',
            CardNumber: '',
            ExpirationDate: '',
            Code: ''
        })
    },

    validationSchema: Yup.object().shape({
        Email: Yup.string()
            .email('E-mail inválido!')
            .required('Digite seu e-mail!'),
        Name: Yup.string()
            .required('Digite seu nome completo!'),
        CardNumber: Yup.string()
            .required('Digite o número do cartão!'),
        ExpirationDate: Yup.string()
            .required('Digite a data de expiração!'),
        Code: Yup.string()
            .required('Digite o código!'),
    }),

    handleSubmit: async (values, { props, setSubmitting }) => {
        console.log('values: ', values);
    },
    enableReinitialize: true
})(PaymentForm);

const mapStateToProps = state => {
    return {
        events: state.events.events
    };
};

const mapDispatchToProps = dispatch =>
    bindActionCreators({ buyEvent }, dispatch);

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(EventPayment); 